@charset "UTF-8";

@import "modules/all";
@import "partials/base";

@import "partials/admin.all";
@import "partials/admin.help";
@import "partials/admin.table";

.geditorial-admin-wrap.geditorial-settings {

	@include main-wrap();

	margin: 10px 20px 0px 2px;
	@include body-rtl() {
		margin: 10px 2px 0px 20px;
	}

	.explanation {

		margin-bottom: 1em;

		p {
			margin: 0.5em 0;

			font-size: 16px;
			line-height: 22px;
		}
	}

	form {

		h3 {
			margin-top: 40px;
		}


		h3 + table.form-table {
			margin-top: -50px;
		}

		.d2escription.no-fields {
			margin-top: 40px;
		}
	}

	.modules {
		margin-top: 20px;
		// min-width: 740px;
		width: 100%;
		overflow: hidden;

		.module-enabled {}

		.module-disabled {
			color: opacity(0.5);
		}

		.module {

			position: relative;
			float: left;
			width: 210px;
			min-height: 150px;
			margin: 0 15px 15px 0;
			border: 1px solid #e1e1e1;
			padding: 10px;
			// background-color: #f1f1f1;
			border-radius: 3px;

			@include body-rtl() {
				float: right;
				margin: 0 0 15px 15px;
			}


			h3 {
				// color: #000000;
				margin: 2px 0 0 0;
				padding: 0;
			}

			h4 {
				// color: #000000;
				font-size: 17px;
				font-style: normal;
				font-weight: normal;
				line-height: 1.1;
				margin: 2px 0 0 0;
				padding: 0;
			}

			p {
				font-size: 12px;
				// color: #3d3d3d;
				line-height: 150%;
			}

			.actions {
				position: absolute;
				bottom: 0px;
				right: 10px;
				width: 210px;
				margin: 0 0 10px 0;
				padding: 0 0 0 20px;

				@include body-rtl() {
					right: auto;
					left: 10px;
					padding: 0 20px 0 0;
				}
			}

			.button {
				font-weight: normal;
				margin: 0;
			}

			.button-primary {
				float: right;
				@include body-rtl() {
					float: left;
				}
			}

			.button-secondary {}
			.button-remove {}
			.button-toggle {}
			.button-configure {}

			.dashicons {
				font-size: 25px;
				width: 25px;
				height: 25px;
				float: right;
				@include body-rtl() {
					float: left;
				}
			}

			.icon {
				height: 24px;
				width: 24px;
				float: right;
			}
		}
	}
}

